<template>
    <div id="chartdiv"></div>
</template>

<script>
import * as am4core from "@amcharts/amcharts4/core";
import * as am4charts from "@amcharts/amcharts4/charts";
import am4themes_dataviz from "@amcharts/amcharts4/themes/dataviz";
import am4themes_dark from "@amcharts/amcharts4/themes/dark";
import am4themes_animated from "@amcharts/amcharts4/themes/animated";

export default {
    name: "TkmChart",
    created() {
        am4core.ready(function() {
            // Themes begin
            am4core.useTheme(am4themes_dataviz);
            am4core.useTheme(am4themes_animated);
            // Themes end

            // Create chart instance
            var chart = am4core.create("chartdiv", am4charts.PieChart3D);

            // Add data
            chart.data = [ {
                    "country": "1st burn",
                    "litres": 10000000000
                }, {
                    "country": "2nd burn",
                    "litres": 15000000000
                }, {
                    "country": "3rd burn",
                    "litres": 25000000000
                }, {
                    "country": "Airdrop",
                    "litres": 5000000000
                }, {
                    "country": "Marketing funds",
                    "litres": 10000000000
                }, {
                    "country": "Team Tokens",
                    "litres": 10000000000
                }, {
                    "country": "Liquidity Pool",
                    "litres": 25000000000
            }];

            // Set inner radius
            chart.innerRadius = am4core.percent(50);
            chart.legend = new am4charts.Legend();
            chart.legend.position = "right";

            // Add and configure Series
            var pieSeries = chart.series.push(new am4charts.PieSeries3D());
            pieSeries.dataFields.value = "litres";
            pieSeries.dataFields.category = "country";
            // pieSeries.slices.template.stroke = am4core.color("#1F1F1F");
            pieSeries.slices.template.strokeWidth = 0;
            // pieSeries.slices.template.strokeOpacity = 1;
            pieSeries.ticks.template.disabled = true;
            pieSeries.labels.template.disabled = true;

            // pieSeries.colors.list = [
            //     am4core.color("#4527A0"),
            //     am4core.color("#558B2F"),
            //     am4core.color("#FF8F00"),
            //     am4core.color("#C62828"),
            //     am4core.color("#6A1B9A"),
            //     am4core.color("#1565C0"),
            // ];

            // This creates initial animation
            pieSeries.hiddenState.properties.opacity = 1;
            pieSeries.hiddenState.properties.endAngle = -90;
            pieSeries.hiddenState.properties.startAngle = -90;

        });

    }
}
</script>

<style>
#chartdiv {
  width: 100%;
  height: 300px;
}

</style>